.teamContainer {
    background-repeat: repeat;
    max-height: fit-content;
    color: white;
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    flex-grow: 1;
}

h3 {
    font-size: 30px;
    margin: 15px;
    font-weight: 600;

}

.container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
}

.container .card {
    width: 340px;
    margin: 10px;
    background: #444;
    border-radius: 10px;
    transition: 0.3s;

}

.container .card:hover {
    background: rgb(31, 92, 92);
}

.container .card .content {
    transition: 0.3s;

}

.container .card .content:hover {
    transform: scale(1.069);
}

.container .card .profile {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    overflow: hidden;
    margin: 20px auto;
    border: 5px rgb(244, 245, 247);
}

.container .card .profile img {
    width: 100%;
   height: 100%;
}